  .button {
    background-color: transparent;
    border: 0;
    outline: 1px solid transparent;
    cursor: pointer;
    padding: 0;
  }

  .panel {
    position: fixed;
    top: 0;
    right: 0;
    width: 450px;
    bottom: 0;
    z-index: 2;
    transform: translateX(450px);
  }

  .open .panel {
    transform: translateX(0);
    transition: all 400ms ease;
  }

  .panel::before,
  .panel::after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    width: 225px;
    transition: all 400ms ease-out;
  }

  .panel::before {
    clip-path: polygon(100% 10%, 100% 0, 100% 100%, 100% 90%);
    left: 0;
  }

  .panel::after {
    background: #f0f0f0 linear-gradient(to right, #f7f7f7 0%, #fff 100%);
    clip-path: polygon(100% 50%, 100% 0, 100% 100%, 100% 50%);
    right: 0;
  }

  .open .panel::before,
  .open .panel::after {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }

  .open .panel::before {
    transition-delay: 400ms;
    background: #f0f0f0 linear-gradient(to right, #f3f3f3 0%, #f1f1f1 48%, #f1f1f1 100%);
  }

  .body {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    left: 0;
    background-color: #fff;
    transition: all 300ms cubic-bezier(0.22, 0.61, 0.36, 1);
    z-index: 1;
    clip-path: polygon(50% 50%, 50% 50%, 50% 50%, 50% 50%);
  }

  .open .panel .body {
    transition-delay: 0.8s;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
  }

  .overlay {
    background-color: rgba(0, 0, 0, 0.2);
    position: fixed;
    visibility: hidden;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    opacity: 0;
  }

  .open .panel + .overlay {
    opacity: 1;
    visibility: visible;
    transition: all 400ms ease;
  }
